<template>
  <main class="content container" v-if="orderInfo">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'order' }">
             
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
          <span v-if="orderInfo.id"> {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;  
             &nbsp; .    &nbsp;
            &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">  </span>
              <span class="dictionary__value" v-if="orderInfo.name">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">   </span>
              <span class="dictionary__value" v-if="orderInfo.address">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">  </span>
              <span class="dictionary__value" v-if="orderInfo.phone">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value" v-if="orderInfo.email">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">   </span>
              <span class="dictionary__value">    </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" v-if="orderInfo.basket.items">
            <li
              class="cart__order"
              v-for="item in orderInfo.basket.items"
              :key="item.product.id"
            >
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price || numberFormat }} </b>
              <span>: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>
              : <b>{{ orderInfo.basket.items.length }}</b>   
              <b>{{ orderInfo.totalPrice || numberFormat }} </b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";

export default {
  data() {
    return {
      orderInfo: {},
    };
  },

  filters: { numberFormat },

  watch: {
    "$store.state.orderInfo": {
      handler() {
        this.orderInfo = this.$store.state.orderInfo;
      },
      immediate: true,
    },
  },

  created() {
    if (
      this.$store.state.orderInfo &&
      this.$store.state.orderInfo === this.$route.params.id
    ) {
      this.orderInfo = this.$store.state.orderInfo;
      return;
    }
    this.$store.dispatch("loadOrderInfo", this.$route.params.id);
    this.orderInfo = this.$store.state.orderInfo;
  },
};
</script>
